import React from "react";
import $ from "jquery";

class Home extends React.Component {
  render() {
    return (
      <React.Fragment>
        <div
          className="d-flex flex-column flex-md-row justify-content-start align-items-cener bg-light"
          id="home-main"
          style={{ minHeight: "92vh" }}
        >
          <div className=" p-3  d-flex align-items-start flex-column w-100">
            <div className="ml-5 animate__animated animate__slideInLeft mt-5">
              <h1 className="m-0  " id="home-head">
                brand title
              </h1>
            </div>
            <div className="ml-5 animate__animated animate__slideInLeft">
              <p className=" m-0 " id="home-head-p">
                brand description or brand slogan
              </p>
            </div>

            <div className=" ml-5 mt-5 p-0 d-flex flex-column justify-content-start align-items-start animate__animated animate__fadeIn">
              <h2 id="collapseButtonOne" className="btn btn-mute">
                <i id="pointerOne" class="bi bi-caret-right-fill"></i>
                Why brand tilte?
              </h2>
              <div id="collapseOne" className="card card-body">
                <p className="ml-3 m-0">option one</p>
                <p className="ml-3 m-0">option two</p>
                <p className="ml-3 m-0">option three</p>
                <p className="ml-3 m-0">option four</p>
              </div>
            </div>

            <div className="ml-5 mt-3 p-0 d-flex flex-column justify-content-start align-items-start animate__animated animate__fadeIn">
              <h2 id="collapseButtonTwo" className="btn btn-mute card-head">
                <i id="pointerTwo" class="bi bi-caret-right-fill"></i>
                our path
              </h2>
              <div id="collapseTwo" className="card card-body">
                <p className="ml-3 m-0">path one</p>
                <p className="ml-3 m-0">path two</p>
                <p className="ml-3 m-0">path three</p>
                <p>
                  <p className="ml-3 m-0">path four</p>
                </p>
              </div>
            </div>

            <div className="ml-5 mt-3 p-0 d-flex flex-column justify-content-start align-items-start animate__animated animate__fadeIn">
              <h2 id="collapseButtonThree" className="btn btn-mute card-head">
                <i id="pointerThree" class="bi bi-caret-right-fill"></i>
                our poduct
              </h2>
              <div id="collapseThree" className="card card-body">
                <p className="ml-3 m-0">product one</p>
                <p className="ml-3 m-0">product two</p>
                <p className="ml-3 m-0">product three</p>
                <p>
                  <p className="ml-3 m-0">product four</p>
                </p>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
  componentDidMount() {
    $("document").ready(() => $("#collapseOne").slideUp());
    $("document").ready(() => $("#collapseTwo").slideUp());
    $("document").ready(() => $("#collapseThree").slideUp());

    $("#collapseButtonOne").click(() => {
      $("#collapseOne").slideToggle();
      if ($("#pointerOne")[0].className === "bi bi-caret-down-fill") {
        $("#pointerOne").removeClass("bi bi-caret-down-fill");
        $("#pointerOne").addClass("bi bi-caret-right-fill");
      } else {
        $("#pointerOne").removeClass("bi bi-caret-right-fill");
        $("#pointerOne").addClass("bi bi-caret-down-fill");
      }
    });

    $("#collapseButtonTwo").click(() => {
      $("#collapseTwo").slideToggle();
      if ($("#pointerTwo")[0].className === "bi bi-caret-down-fill") {
        $("#pointerTwo").removeClass("bi bi-caret-down-fill");
        $("#pointerTwo").addClass("bi bi-caret-right-fill");
      } else {
        $("#pointerTwo").removeClass("bi bi-caret-right-fill");
        $("#pointerTwo").addClass("bi bi-caret-down-fill");
      }
    });

    $("#collapseButtonThree").click(() => {
      $("#collapseThree").slideToggle();
      if ($("#pointerThree")[0].className === "bi bi-caret-down-fill") {
        $("#pointerThree").removeClass("bi bi-caret-down-fill");
        $("#pointerThree").addClass("bi bi-caret-right-fill");
      } else {
        $("#pointerThree").removeClass("bi bi-caret-right-fill");
        $("#pointerThree").addClass("bi bi-caret-down-fill");
      }
    });
  }
}

export default Home;
